/* eslint-disable jsx-a11y/anchor-is-valid */
import  React,{FC, useState, useEffect} from 'react'
import {useIntl} from 'react-intl'
import {Link, withRouter} from 'react-router-dom'
import {message, Skeleton} from 'antd'
import {PageTitle} from '../../../_metronic/layout/core'
import {KTSVG} from '../../../_metronic/helpers'
import moment from 'moment'
import {mainConfig} from '../../../config/mainConfig'

let usrRole = '',
  token = ''



const TagWrapper: FC = () => {
  const intl = useIntl()

  const [ErrName, setErrName] = useState<any>(false)
  const [loading, setloading] = useState<any>(false)

  const [tagData, setTagData] = useState<any[]>([])
  const [tagTableData, setTagTableData] = useState<any[]>([])

  const [tagName, setTagName] = useState<any>('')
  const [editTagName, setTegditTagName] = useState<any>('')
  const [editTagData, settagEditData] = useState<any>({})

  useEffect(() => {
    setloading(true)
    getTags()
  }, [])

  const getTags=()=> {
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/tags`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            localStorage.clear()
            window.location.reload()
          }

          response.json().then(function (data) {
           
            setTagData(data.data)
            setTagTableData(data.data)
            setloading(false)
          })
        })
        .catch(function (err) {})
    } catch (err) {}
  }

  const createTag=()=>{
    if (tagName != '') {
      const tag = {
        name: `${tagName}`,
      }
      setloading(true)

      try {
        fetch(`${mainConfig.MAIN_ENDPOINT}/api/create-tag`, {
          method: 'post',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(tag),
        })
          .then(function (data) {
            data.json().then(function (data) {
              getTags()
              setTagName('')
              setloading(false)
              message.success(data.message)
            })
            if (data.status == 204) {
              getTags()
              setTegditTagName('')
              setloading(false)
              message.error('Tag Already Exist !')
            }
          })
          .catch(function (error) {
            setloading(false)
          })
      } catch (err) {}
      $('#add_tag').modal('hide')
    } else {
      setErrName(true)
    }
  }

  const updateTag=() =>{
    if (editTagName != '') {
      setloading(true)
      const tag = {
        name: `${editTagName}`,
      }

      try {
        fetch(`${mainConfig.MAIN_ENDPOINT}/api/update-tag/${editTagData.tag_id}`, {
          method: 'put',
          headers: {
            Authorization: `Bearer ${token}`,
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(tag),
        })
          .then(function (data) {
            data.json().then(function (res) {
              if (res.success == true) {
                message.success(res.message)
                getTags()
                setTegditTagName('')
                setloading(false)
              }
            })
            if (data.status == 204) {
              getTags()
              setTegditTagName('')
              setloading(false)
              message.error('Tag Already Exist !')
            }
          })
          .catch(function (error) {
          
            setloading(false)
            getTags()
          })
      } catch (err) {
        console.log('ERR>>>', err)
      }
      $('#edit_tag').modal('hide')
    } else {
      setErrName(true)
    }
  }

  const editTag=(data: any)=> {
    setTegditTagName(data.name)
    settagEditData(data)
  }

  const onSearchProduct=(e: any)=> {
    let n = e.target.value
    let searchdata = new Array()

    tagData.map((i, j) => {
      if (i.name.toLowerCase().includes(n.toLowerCase())) {
        searchdata.push(i)
      }
    })

    setTagData(searchdata)

    if (n == '') {
      setTagData(tagTableData)
    }
  }

  const onDelete=(values: any)=> {
    setloading(true)
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/delete-tag/${values.tag_id}`, {
        method: 'delete',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (data) {
          data.json().then(function (res) {
            message.success(res.message)

            getTags()
            setTegditTagName('')
            setloading(false)
          })
        })
        .catch(function (error) {
          setloading(false)
        })
    } catch (err) {
      console.log('ERR>>>', err)
    }
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')
  usrRole = JSON.parse(localStorage.getItem('portalUserDataRole') || '')

  return (
    <React.Fragment>
      {loading == true ? (
        <React.Fragment>
          <Skeleton loading={loading} active></Skeleton>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
            <Link to='/home' className='pe-1 cursor-pointer'>
              <li className='breadcrumb-item pe-1'>Home</li>
            </Link>
            / &nbsp;<li className='breadcrumb-item pe-3'>Tag Management</li>
          </ol>
          <PageTitle breadcrumbs={[]}>{intl.formatMessage({id: 'MENU.DASHBOARD'})}</PageTitle>

          <div className='row g-5 gx-xxl-8' style={{marginTop: '1%'}}>
            <div className='col-xxl-12'>
              <div className={`card`}>
                <div className='card-header border-0 pt-2'>
                  <h3 className='card-title align-items-start flex-column'>
                    <span
                      className='card-label fw-bolder fs-3 mb-1'
                      style={{color: 'rgb(138, 38, 80)'}}
                    >
                      Tags
                    </span>
                  </h3>

                  <div className='card-toolbar'>
                    <input
                      type='search'
                      onChange={onSearchProduct}
                      className='form-control form-control-solid'
                      placeholder='Search by  Tag Name'
                    />
                  </div>

                  <div className='card-toolbar'></div>
                  {usrRole == 'Administrator' && (
                    <div
                      className='card-toolbar'
                      data-bs-toggle='tooltip'
                      data-bs-placement='top'
                      data-bs-trigger='hover'
                      title='Click to add new Tag'
                    >
                      <button
                        type='button'
                        className='btn btn-sm btn-primary'
                        data-kt-menu-trigger='click'
                        data-kt-menu-placement='bottom-end'
                        data-kt-menu-flip='top-end'
                        data-bs-toggle='modal'
                        data-bs-target='#add_tag'
                      >
                        <KTSVG
                          path='/media/icons/duotune/arrows/arr075.svg'
                          className='svg-icon-3'
                        />
                        New Tag
                      </button>
                    </div>
                  )}
                </div>

                <div className='card-body py-3'>
                  <div className='table-responsive h-600px table-scroll'>
                    <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                      <thead>
                        <tr className='fw-bolder text-muted'>
                          <th className='w-25px'></th>

                          <th className='min-w-100px'>Tag</th>

                          <th className='min-w-100px'>Created At</th>

                          {usrRole == 'Administrator' && (
                            <>
                              <th className='min-w-50px text-end'>Status</th>
                              <th className='min-w-30px text-end'>Edit</th>
                            </>
                          )}
                        </tr>
                      </thead>

                      <tbody>
                        {tagData != undefined &&
                          tagData.map((j) => {
                       
                            return (
                              <tr>
                                <td></td>
                                <td>
                              {
                                j.mapped ==true?
                                <Link to={{pathname: '/tagDetails', state: j}}>
                                <span className='text-danger fw-bolder text-hover-primary d-block fs-6 text-decoration-underline link'>
                                  {j.name}
                                </span>
                              </Link>:<>{j.name}</>
                              }
                                  
                                </td>

                                <td>
                                  <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                    {moment(j.created_at).format('DD-MMM-YYYY')}
                                  </span>
                                </td>

                                {usrRole == 'Administrator' && (
                                  <>
                                    <td>
                                      <div className='d-flex justify-content-end flex-shrink-0'>
                                        <div
                                          className='form-check form-switch'
                                          title={
                                            j.is_deleted == 1 ? 'Activate Tag' : 'Deactivate Tag'
                                          }
                                        >
                                          <input
                                            className='form-check-input'
                                            type='checkbox'
                                            id='flexSwitchCheckChecked'
                                            checked={j.is_deleted == 1 ? false : true}
                                            onChange={() => onDelete(j)}
                                          />
                                        </div>
                                      </div>
                                    </td>

                                    <td>
                                      <div className='d-flex justify-content-end flex-shrink-0'>
                                        <button
                                          className='btn btn-icon btn-bg-light btn-active-color-primary btn-sm me-1'
                                          data-kt-menu-trigger='click'
                                          data-bs-toggle='modal'
                                          data-bs-target='#edit_tag'
                                          onClick={() => editTag(j)}
                                        >
                                          <KTSVG
                                            path='/media/icons/duotune/art/art005.svg'
                                            className='svg-icon-3'
                                          />
                                        </button>
                                      </div>
                                    </td>
                                  </>
                                )}
                              </tr>
                            )
                          })}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className='modal fade' id='add_tag' aria-hidden='true'>
            <div className='modal-dialog mw-650px'>
              <div className='modal-content'>
                <div className='modal-header pb-0 border-0 justify-content-end'>
                  <div
                    className='btn btn-sm btn-icon btn-active-color-primary'
                    data-bs-dismiss='modal'
                  >
                    <KTSVG path='/media/icons/duotune/arrows/arr061.svg' className='svg-icon-1' />
                  </div>
                </div>

                <div className='modal-body scroll-y mx-5 mx-xl-18 pt-0 pb-15'>
                  <div className='text-center mb-13'>
                    <h1 className='mb-3'>Add New Tag</h1>
                  </div>
                  <form className='tag-form'>
                    <div className='fv-row mb-10'>
                      <label className='form-label fs-6 fw-bolder required text-dark text-left'>
                        Tag :
                      </label>
                      <input
                        type='input'
                        value={tagName}
                        onChange={(e) => setTagName(e.target.value)}
                        onFocus={() => setErrName(false)}
                        className='form-control form-control-solid'
                        placeholder=''
                      />
                      {ErrName == true ? (
                        <span role='alert' className='text-danger'>
                          Tag Name is Required
                        </span>
                      ) : null}
                    </div>

                    <button type='button' onClick={createTag} className='btn btn-primary'>
                      Submit
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>

          <div className='modal fade' id='edit_tag' aria-hidden='true'>
            <div className='modal-dialog mw-650px'>
              <div className='modal-content'>
                <div className='modal-header pb-0 border-0 justify-content-end'>
                  <div
                    className='btn btn-sm btn-icon btn-active-color-primary'
                    data-bs-dismiss='modal'
                  >
                    <KTSVG path='/media/icons/duotune/arrows/arr061.svg' className='svg-icon-1' />
                  </div>
                </div>

                <div className='modal-body scroll-y mx-5 mx-xl-18 pt-0 pb-15'>
                  <div className='text-center mb-13'>
                    <h1 className='mb-3'>Edit Tags</h1>
                  </div>
                  <form className='tag-form'>
                    <div className='fv-row mb-10'>
                      <label className='form-label fs-6 fw-bolder required text-dark text-left'>
                        Tag :
                      </label>
                      <input
                        type='input'
                        value={editTagName}
                        onChange={(e) => setTegditTagName(e.target.value)}
                        onFocus={() => setErrName(false)}
                        className='form-control form-control-solid'
                        placeholder=''
                      />
                      {ErrName == true ? (
                        <span role='alert' className='text-danger'>
                          Tag Name is Required
                        </span>
                      ) : null}
                    </div>

                    <button type='button' onClick={updateTag} className='btn btn-primary'>
                      Submit
                    </button>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(TagWrapper)
